* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

h1 {
  margin-top: 0;
  font-size: 22px;
}

h2 {
  margin-top: 0;
  font-size: 20px;
}

h3 {
  margin-top: 0;
  font-size: 18px;
}

h4 {
  margin-top: 0;
  font-size: 16px;
}

h5 {
  margin-top: 0;
  font-size: 14px;
}

h6 {
  margin-top: 0;
  font-size: 12px;
}

code {
  font-size: 1.2em;
}

ul {
  -webkit-padding-start: 20px;
          padding-inline-start: 20px;
}

body {
  --desktop-item-width: 150px;
  --desktop-item-height: 80px;
  --desktop-max-item-size: 200px;
}

.board {
  width:100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-left: 20%;
  /* flex-wrap: wrap; */
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-pack: normal;
      justify-content: normal;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
}


.tile {
  /* flex grid */
  display: block;
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  -ms-flex-preferred-size: auto;
      flex-basis: auto;
  -ms-flex-item-align: auto;
      align-self: auto;
  -ms-flex-order: 0;
      order: 0;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;

  /* font */
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 40px;
  /* font-weight: bold; */
  text-align: center;

  /* spacing & size */
  width: var(--desktop-item-width);
  max-width: var(--desktop-max-item-size);
  min-height: 70px;
  height: var(--desktop-item-height);
  width: var(--desktop-item-width);

  /* borders */
  margin: 1%;
  border-radius: 15px;
  border: 0px;

  /* colors */
  background-color: #EFEFE6;
}

.tile:focus {
  background-color: #DADED4;
  border: 0px;
  outline: none;
}

.tile-guessed {
  background-color: #C4CCC2; 
}

.tile-anchor {
  background-color: #C4CCC2;
}





















.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
}
.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.game-info {
  margin-left: 20px;
}



